<script>
import { getSubgroupProductInfos } from '@/api/commodity/commodityList'
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      active: "", //绑定样式
      deleteBtn: false
    };
  },
  created(){
    if(this.itemData.commodityIds.length > 0) this.getData()
  },
  methods: {
    getData(){
      console.log('商品组件请求了！')
      getSubgroupProductInfos(this.itemData.commodityIds.join(',')).then(res=>{
        console.log('商品组件数据回显',res)
        if(res.code == 200){
          this.itemData.goodsData = res.data;
        }
      })
    },
    // 移入事件
    mouseOver() {
      this.active = "active";
      this.deleteBtn = true;
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
      this.deleteBtn = false;
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    }
  }
};
</script>

